@media only screen and (min-width: 992px)
{
    .iti-logo{
        width: 50%;
        height: 50%;
        margin-left: 20%;
    }

    .iti-home-centered{
        position: absolute;
        top: 30%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: white;
      }
      .carousel-inner{
        height: 20%;
        width: 50%;
        font-size: 30px;
    }
    
   .iti-home-section-1-form-control{
       height: 60px !important;
   }
   .iti-home-section-1-btn{
       color: white;
       background-color: #3700ff !important;
   }
     
  .iti-btn{
      width:150px;
     
  }
  .iti-card-image{
      height:200px;
    
  }

  h5{
      color:blue;
  }
  h6{
      color:#333333;
  }
  .iti-row{
      margin-top: 50px;
  }

  .iti-footer-icon-ul{
    display: flex;
    list-style: none;
    margin-left: 500px;
    margin-top: 20px;
}

.iti-footer-icon-li{
    padding: 10px;
}



.iti-footer-section2-link {
    font-size: 15px;
    color: rgba(2, 3, 8, 0.6);
    list-style: none;
    padding: 5px;
}
  .iti-nav-dropdown{
    width: 50px;
    margin: 8px;
    background-repeat: no-repeat;
}

.iti-nav-dropdown a::after{
    content: unset;
}

.iti-card-horizontal {
    display: flex;
    flex: 1 1 auto;
}
.iti-ridge { 
    border-style: groove;
}
.iti-content{
    text-align: left;
}  

}
