@media only screen and (max-width: 414px) {
    .iti-logo{
        height: 40%;
        width: 40%;;
    }

    .iti-desktop-login-btn{
        display: none !important;
    }

    .iti-3dots{
        display: none;
    }
    
    .iti-nav-dropdown a::after{
        content: unset;
    }

    .iti-footer-li-link{
        list-style: none;
        font-size: 15px;
        letter-spacing: -0.28px;
        font-size: 12px;
        margin-top: 10px;
        color: rgba(44, 47, 66, 0.6)
    }
    .iti-footer-li-columnname
    {
        list-style: none;
        font-size: 12px;
        color: black;
        margin: 8px 0px 0px;
    }

    
    .iti-footer-section2-link {
        font-size: 12px;
        color: rgba(44, 47, 66, 0.6);
        list-style: none;
        padding: 5px;
    }
    .iti-footer-section2-ul{
        font-size: 12px;
        display: block;
    }

    .iti-footer-menu{
        width: 100%;
    }
    .iti-btn{
        width:150px;
        /* margin-top: 20px; */
        
    }
    .iti-card-image{
        height:200px;
        
    }
  
    h5{
        color:blue;
    }
    h6{
        color:#333333;
    }
    .iti-row{
        margin-top: 50px;
    }

    .iti-footer-left{
        float: left;
    }

    .iti-footer-icon-ul{
        display: none;
    }

    .iti-footer-icon-li{
        padding: 5px;
    }
   
    .iti-login-btn{
      margin-left: 70px;
    }

    .iti-mobile-login-btn{
        margin-left: 50px;
    }
    .iti-home-centered{
        position: absolute;
        top: 58%;
        left: 38%;
        transform: translate(-30%, -70%);
        color: white;
      }
      .iti-home-inline{
          font-size: 15px;
      }
      .iti-home-section-1-form-control{
        height: 45px !important;
    }
    .iti-home-section-1-btn{
        color: white;
        background-color: #3700ff !important;
    }
    .carousel-inner{
        height: 20%;
        width: 50%;
        font-size: 17px;
    }
  }