@media only screen and (max-width: 991px) and (min-width: 415px)
{

    .iti-logo{
        height: 40%;
        width: 40%;
        margin-left: 50px;
    }

    .iti-desktop-login-btn{
        display: none !important;
    }


    .iti-nav-dropdown a::after{
        content: unset;
    }

    .iti-footer-li-link{
        list-style: none;
        font-size: 15px;
        letter-spacing: -0.28px;
        font-size: 12px;
        margin-top: 10px;
        color: rgba(44, 47, 66, 0.6)
    }

    .iti-footer-li-columnname
    {
        list-style: none;
        font-size: 15px;
        color: black;
        margin: 8px 0px 0px;
    }

    .iti-footer-section2-link {
        font-size: 15px;
        color: rgba(44, 47, 66, 0.6);
        list-style: none;
        padding: 5px;
    }

    .iti-footer-section2-ul{
        display: block;
    }

    .iti-footer-menu{
        width: 100%;
    }

    .iti-footer-left{
        float: left;
    }

    .iti-footer-icon-ul{
        display: flex;
        list-style: none;
        margin-left: 100px;
    }

    .iti-footer-icon-li{
        padding: 5px;
    }

    .iti-mobile-login-btn{
        margin-left: 300px;
    }
  
    .iti-home-centered{
        position: absolute;
        top: 55%;
        left: 52%;
        transform: translate(-50%, -50%);
        color: white;
      }
      .iti-home-section-1-form-control{
        height: 80px !important;
    }
    .iti-home-section-1-btn{
        color: white;
        background-color: orange !important;
    }
     
    .iti-btn{
        width:150px;
        margin-bottom: 12px;
    }
    .iti-card-image{
        height:150px;
        width:157px;
    }
    .iti-card{
        height: 330px;
        width:280px;
    }
    h5{
        color:blue;
    }
    h6{
        color:#333333;
    }
    .iti-row{
        margin-top: 50px;
    }
    .carousel-inner{
        height: 20%;
        width: 50%;
        font-size: 25px;
    }
}

